body {
  max-width: 100%;
}

.musicCard {
  backdrop-filter: blur(5px);
  background-color: rgba(255, 255, 255, 0.7);
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.3) 0 0 10px 2px;
  margin: 10px 0 10px 10px;
  padding: 0 30px;
  width: 300px;
}

@media only screen and ( max-width : 500px ) {

  .musicCard {
    max-width: 270px;
  }
}
